import { Box, Button, Hide, Image, SimpleGrid, StylesProvider, Text } from '@chakra-ui/react'
import React from 'react'
import PROFILE from "./image/PROFILE.jpg"
import Styles from "./about.module.css"
import Typical from "react-typical"
import Resume from "./image/Amit_Singh_Chouhan_resume.pdf"
import { FaLaptopCode } from 'react-icons/fa'
// import Wave from "./image/Wave.gif"






const About = () => {
  return (
    <>
    {/* <SimpleGrid  columns={[1, 1, 2]} w="95%" margin="auto"  justifyContent="space-between" marginTop={"120px"} boxShadow='lg' p='6' rounded='md' bg='white' backgroundImage={Wave} backgroundSize="65%" */}
    <SimpleGrid columns={[1, 1, 2]}  w="95%" margin="auto" marginTop={"120px"}  boxShadow='lg' p='6' rounded='md' bg='white' >
    <Box height={"200px"} width={["400px","auto","600px"]}  padding={"1em"} className={Styles.texts}  >

        <Text color={"black"}>
          Welcome to my Portfolio
        </Text>
        <Text color={"black"}>
         Hi, I'm Amit Singh Chouhan
        </Text >
        <Hide below='md'>
        <Text color={"black"} display="flex"><Box paddingTop={"10px"}marginRight="12px" ><FaLaptopCode /></Box>
       <Typical   
      loop={Infinity}
      steps={[
        "A Full Stack Web Developer",1500,
        "MERN Stack Developer",1500
      ]} />
        </Text>
        </Hide>
        <a  href={Resume} download>  <Button position={"revert-layer"}  mt={"2rem"} w={"120px"}>Resume</Button></a>
     

    </Box>

        <Box padding={"1em"} width="400px" m={"auto"} >
        <Image borderRadius={"50%"}  src={PROFILE} width="400px" m={"auto"}/>
        </Box>



        </SimpleGrid>
</>
  )
}

export default About