import { Box, Center, SimpleGrid } from '@chakra-ui/react'
import React from 'react'
import chakra from "./images/chak.jpg"
import css from "./images/css.jpg"
import express from "./images/express.png"
import html from "./images/html.jpg"
import javascript from "./images/javascript.jpg"
import mongodb from "./images/mongodb.png"
import nodejs from "./images/nodejs.jpg"
import rect from "./images/rect.png"
import Styles from "./skills.module.css"


const skills=[
    {
        title:"Chakra-ui",
        logo:chakra
    },
    {
        title:"CSS",
        logo:css
    },
    {
        title:"Express",
        logo:express
    },
    {
        title:"HTML",
        logo:html
    },
    {
        title:"JavaScript",
        logo:javascript
    },
    {
        title:"MongoDB",
        logo:mongodb
    },
    {
        title:"NodeJS",
        logo:nodejs
    },
    {
        title:"React",
        logo:rect
    }
]





const Skills = () => {
  return (
    <>
    <div style={{backgroundColor:"yellow",marginTop:"1em", paddingTop:"5em" ,paddingBottom:"5em"}}>
    {/* <div  className={Styles.box}> */}
    <SimpleGrid columns={[2, 3, 4]} spacing='40px'  border="5px solid white"  width="95%" margin=" auto" borderRadius="50px" padding="1em">
            {
                skills.map((el)=>{

                    return(<Box >
                        <img  style={{borderRadius:"50%", margin:"auto" ,width:"100px"}} src={el.logo}/>
                        <h3 style={{textAlign:"Center" }}>{el.title}</h3>
                    </Box>)
                })
            }


</SimpleGrid>
    {/* </div> */}
    </div>

    </>
  )
}

export default Skills