import { Box, Hide, Show, Text } from '@chakra-ui/react'
import React from 'react'
import DrawerExample from './Drawer'

const Navbar = () => {
  return (
    <>
    <Hide below="md">
    <Box  display="flex" justifyContent="space-between" paddingTop="8px">

    <Hide below='lg'>
    <Text mr={"20px"}  color={"grey" } fontWeight="bold" fontSize="2xl">About me</Text>
    <Text mr={"20px"} color={"grey" } fontWeight="bold" fontSize="2xl">Skills</Text>
    <Text mr={"20px"} color={"grey" } fontWeight="bold" fontSize="2xl">Projects</Text>
    </Hide>
    <Text mr={"20px"} color={"grey" } fontWeight="bold" fontSize="2xl">Contact</Text>
    </Box>
    </Hide>
    <Show below="md">
    <DrawerExample />
    </Show>
    </>
  )
}

export default Navbar