import React from "react";
import { Box, Flex, SimpleGrid } from "@chakra-ui/react";
import { Text } from "@chakra-ui/react";
import chakra from "./images/chak.jpg";
import css from "./images/css.jpg";
import express from "./images/express.png";
import html from "./images/html.jpg";
import javascript from "./images/javascript.jpg";
import mongodb from "./images/mongodb.png";
import nodejs from "./images/nodejs.jpg";
import rect from "./images/rect.png";

const frontend = [
  {
    title: "Chakra-ui",
    logo: chakra,
  },
  {
    title: "CSS",
    logo: css,
  },

  {
    title: "HTML",
    logo: html,
  },
  {
    title: "JavaScript",
    logo: javascript,
  },

  {
    title: "React",
    logo: rect,
  },
];

const backend = [
  {
    title: "Express",
    logo: express,
  },
  {
    title: "MongoDB",
    logo: mongodb,
  },
  {
    title: "NodeJS",
    logo: nodejs,
  },
];


const container = {
  hidden: { opacity: 1, scale: 0 },
  visible: {
    opacity: 1,
    scale: 1,
    transition: {
      delayChildren: 0.3,
      staggerChildren: 0.2
    }
  }
};

const item = {
  hidden: { y: 20, opacity: 0 },
  visible: {
    y: 0,
    opacity: 1
  }
};


const PersonalSkills = () => {
  return (
    <Box mt={'100px'} marginBottom={"100px"}>
      <SimpleGrid columns={[1,1,2]} rowGap={"20px"} >
        <Box style={{width:"75%" ,marginLeft:"auto", marginRight:"auto"}} >
          <Text fontWeight={600} padding="1em" border={"5px solid yellow"} textAlign="center" borderRadius={"20px"} marginBottom="20px">Frontend Web Development</Text>
          {frontend.map((el)=>(<Flex  marginLeft={"50px"} marginBottom={"20px"}><img width="70px" src={el.logo} alt="logo"/><h3 style={{marginLeft:"50px", paddingTop:"20px" , fontWeight:"600"}}>{el.title}</h3></Flex>))}
        </Box>

        <Box style={{width:"75%", marginLeft:"auto", marginRight:"auto"}}>
         
          <Text fontWeight={600} padding="1em" border={"5px solid yellow"} textAlign="center" borderRadius={"20px"} marginBottom="20px">Backend Web Development</Text>
          {backend.map((el)=>(<Flex marginLeft={"50px"} marginBottom={"20px"} ><img width="70px" src={el.logo} alt="logo" /><h3 style={{marginLeft:"50px", paddingTop:"20px" , fontWeight:"600"}}>{el.title}</h3></Flex>))}
        </Box>
      </SimpleGrid>

      <Box></Box>
    </Box>
  );
};

export default PersonalSkills;
