import { Box, Button, Flex, SimpleGrid, Text } from '@chakra-ui/react'
import React from 'react'
import { Carousel } from 'react-responsive-carousel'
import "react-responsive-carousel/lib/styles/carousel.min.css";
// import Carousal from './Carousal'
import Screenshot1 from "./zara/Screenshot1.png"
import Screenshot2 from "./zara/Screenshot2.png"
import Screenshot3 from "./zara/Screenshot3.png"
import Screenshot11 from "./unsplash/Screenshot1.png"
import Screenshot22 from "./unsplash/Screenshot2.png"
import Screenshot33 from "./unsplash/Screenshot3.png"



const Project = () => {




  return (
    <>
    <Box paddingBottom={"100px"}>
    <SimpleGrid columns={[1,1,2]} w={"95%"} margin="auto" spacingX={"50px"} spacingY={"50px"} >

        {/* Poject 1 */}
        <Box padding={"3em"} borderRadius="40px"  boxShadow="rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset">
            <Carousel
                // style={{position:"zIndex" }}
              showThumbs={false}
              autoPlay={true}
              transitionTime={2}
              infiniteLoop={true}
              showStatus={false}
              showArrows={true}>
              <div >
                <img  style={{ borderRadius: "7px", height: "300px" , position:"zIndex" }} src={Screenshot1} alt="outlook1" />
              </div>
              <div >
                <img style={{ borderRadius: "7px", height: "300px" , position:"zIndex" }} src={Screenshot2} alt="outlook1" />
              </div>
              <div >
                <img style={{ borderRadius: "7px", height: "300px", position:"zIndex"}} src={Screenshot3} alt="outlook1" />
              </div>
            </Carousel>

            <Box>
                <h1 style={{fontWeight:"600", padding:".5em"}}>ZARA CLONE</h1>
                <h2 style={{fontWeight:"600", padding:".5em"}}>About : specialises in fast fashion, and sells clothing, accessories, shoes, beauty products and perfumes.</h2>
                <h2 style={{fontWeight:"600", padding:".5em"}}>Tech stack</h2>
                <h2 style={{fontWeight:"600", padding:".5em"}}>REACT | REACT-ROUTER-DOM | CHAKRA UI | REACT-ICON | JSON-SERVER</h2>
                <Flex ml={"10px"} columnGap={"40px"} >
                    <Button position={"revert-layer"} width='120px'>Git</Button>
                    <Button position={"revert-layer"} width='120px' color={"red"}>Demo</Button>
                </Flex>
            </Box>

        </Box>


            {/* Project 2 Unsplash */}
            <Box padding={"3em"} borderRadius="40px"  boxShadow="rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset">
        <Carousel
              showThumbs={false}
              autoPlay={true}
              transitionTime={2}
              infiniteLoop={true}
              showStatus={false}
              showArrows={true}>
              <div>
                <img style={{ borderRadius: "7px", height: "300px"}} src={Screenshot11} alt="outlook1" />
              </div>
              <div>
                <img style={{ borderRadius: "7px", height: "300px"}} src={Screenshot22} alt="outlook1" />
              </div>
              <div>
                <img style={{ borderRadius: "7px", height: "300px"}} src={Screenshot33} alt="outlook1" />
              </div>
            </Carousel>

            <Box>
                <h1 style={{fontWeight:"600", padding:".5em"}}>UNSPLASH CLONE</h1>
                <h2 style={{fontWeight:"600", padding:".5em"}}>About : High-quality Pictures uploading website</h2>
                <h2 style={{fontWeight:"600", padding:".5em"}}>Tech stack :</h2>
                <h2 style={{fontWeight:"600", padding:".5em"}}>JavaScript | HTML | CSS | JSON-Server</h2>
                <Flex ml={"12px"} columnGap={"40px"} >
                    <Button width='120px'>Git</Button>
                    <Button width='120px' color={"red"}>Demo</Button>
                </Flex>
            </Box>



        </Box>



    </SimpleGrid>
    </Box>
    </>
  )
}

export default Project